.todos__footer{
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2%;
    font-size: 12px;
    color: darkgrey;
    
    .todos__items-left{
  }
  .todos__footer-filter{
      display: flex;
      align-items: center;
  }
  .todos__filter-button{
      cursor: pointer;
      padding: 2px;
      margin-left: 6px;
      margin-right: 6px;
      border-radius: 2px;
      border: 1px transparent solid;
  }
  .todos__filter-button-false:checked + .todos__filter-button{
      border: 1px solid darkgray;
      border-width: thin;
  }
  .todos__filter-button-false:focus + .todos__filter-button{
      box-shadow: 0 0 2px rgba(0,0,0,0.5);
  }
  .todos__filter-button-false{
      position: absolute;
      z-index: -1;
      opacity: 0;
  }
  
  .todos__all-completed{
      cursor: pointer;
      background: transparent;
      border: none;
      padding: 2px;
      font-size: 12px;
      color: darkgrey;
      font-family: 'Roboto', sans-serif;
  }
  
  .todos__all-completed:focus{
      outline: none;
      box-shadow: 0 0 2px rgba(0,0,0,0.5);
  }
}